import { Participant } from './Participant';

export class Player extends Participant {
    private name: string;
    private index: number;

    constructor(name: string, index: number) {
        super();
        this.name = name;
        this.index = index;
    }

    public getName(): string {
        return this.name;
    }

    public getIndex(): number {
        return this.index;
    }
}